@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

:root {
  background: #1a1a1d;
  color: #f6fafb;
}

.App {
  font-family: 'Open Sans', sans-serif;
}

.navbar {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: teal;
  height: 70px;
}

.navlinks {
  display: flex;
  width: 15%;
  justify-content: space-around;
}


.nav-item {
  color: white;
  text-decoration: none;
  font-size: 18px;
}
.product-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.product-card {
  margin: 20px;
  padding: 10px;
}

.product-title {
  font-size: 36px;
  padding-bottom: 5px;
}

.product-pricing {
  font-size: 28px;
  padding: 10px 0px;
}

.product-features {
  padding-top: 10px;
  font-size: 20px;
}

.feature-list {
  padding: 10px 0px;
}

.feature-item {
  display: block;
  text-decoration: none;
  font-size: 16px;
}

.product-button {
  color: blue;
}
.heading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  margin: 45px;
}

.heading > .header {
  font-size: 48px;
  margin: 15px;
}

.heading > .description {
  font-size: 20px;
}

.team-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.header > span {
  color: teal;
}

.card {
  position: relative;
  text-align: center;
  width: 300px;
  height: 300px;
  margin: 20px;
  padding: 10px;
  transition: all 1s;
}

.card > .name {
  font-size: 24px;
  margin-top: 5px;
}

.card > .title {
  font-size: 14px;
  margin-top: 2px;
}

.image {
  border-radius: 50%;
  height: auto;
  width: 80%;
}

.card:hover {
  cursor: pointer;
  opacity: 0.5;
}

.card > .story {
  font-size: 12px;
  color: orange;
  margin-top: 5px;
}

.dialog-title {
  display: flex;
  padding: 100px;
  justify-content: space-between;
}
.jumbo {
  display: block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.message {
  font-size: 50px;
  margin-bottom: 15px;
}

.message > span {
  font-size: 65px;
  font-weight: 600;
  color: teal;
}

.descriptor {
  font-size: 30px;
  margin-bottom: 15px;
}

