.card {
  position: relative;
  text-align: center;
  width: 300px;
  height: 300px;
  margin: 20px;
  padding: 10px;
  transition: all 1s;
}

.card > .name {
  font-size: 24px;
  margin-top: 5px;
}

.card > .title {
  font-size: 14px;
  margin-top: 2px;
}

.image {
  border-radius: 50%;
  height: auto;
  width: 80%;
}

.card:hover {
  cursor: pointer;
  opacity: 0.5;
}

.card > .story {
  font-size: 12px;
  color: orange;
  margin-top: 5px;
}

.dialog-title {
  display: flex;
  padding: 100px;
  justify-content: space-between;
}