.navbar {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: teal;
  height: 70px;
}

.navlinks {
  display: flex;
  width: 15%;
  justify-content: space-around;
}


.nav-item {
  color: white;
  text-decoration: none;
  font-size: 18px;
}